'use client'

import { useState, useEffect } from 'react'
import { useParams } from 'next/navigation'
import { Navbar } from '@/components/navbar'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import { 
  ChefHat, 
  Clock, 
  ArrowLeft, 
  ArrowRight, 
  Check, 
  Play, 
  Pause, 
  RotateCcw,
  Thermometer,
} from 'lucide-react'
import Link from 'next/link'

interface Recipe {
  id: string
  title: string
  description: string
  category: string
  prepTime: number
  cookTime: number
  servings: number
  difficulty: 'facile' | 'moyen' | 'difficile'
  image?: string
  rating: number
  author: string
  ingredients: string[]
  steps: RecipeStep[]
}

interface RecipeStep {
  id: string
  title: string
  description: string
  image?: string
  duration?: number
  temperature?: number
}

export default function CookRecipePage() {
  const params = useParams()
  const [recipe, setRecipe] = useState<Recipe | null>(null)
  const [currentStep, setCurrentStep] = useState(0)
  const [completedSteps, setCompletedSteps] = useState<Set<number>>(new Set())
  const [isPlaying, setIsPlaying] = useState(false)
  const [timeRemaining, setTimeRemaining] = useState(0)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    // Données de démonstration
    const mockRecipe: Recipe = {
      id: params.id as string,
      title: 'Crème Pâtissière Vanille',
      description: 'La base de toutes les pâtisseries, onctueuse et parfumée à la vanille de Madagascar.',
      category: 'cremes',
      prepTime: 15,
      cookTime: 10,
      servings: 6,
      difficulty: 'facile',
      rating: 4.8,
      author: 'Marie Dubois',
      ingredients: [
        '500ml de lait entier',
        '4 jaunes d\'œufs',
        '80g de sucre en poudre',
        '40g de maïzena',
        '1 gousse de vanille',
        '30g de beurre',
        '1 pincée de sel'
      ],
      steps: [
        {
          id: '1',
          title: 'Préparation des ingrédients',
          description: 'Fendez la gousse de vanille en deux et grattez les graines. Mélangez les jaunes d\'œufs avec le sucre jusqu\'à ce que le mélange blanchisse.',
          duration: 5
        },
        {
          id: '2',
          title: 'Cuisson du lait',
          description: 'Dans une casserole, portez le lait à ébullition avec la gousse de vanille fendue. Retirez du feu et laissez infuser 10 minutes.',
          duration: 15
        },
        {
          id: '3',
          title: 'Mélange des poudres',
          description: 'Ajoutez la maïzena et le sel au mélange œufs-sucre. Fouettez jusqu\'à obtenir un mélange homogène.',
          duration: 2
        },
        {
          id: '4',
          title: 'Temperage',
          description: 'Versez le lait chaud en fouettant constamment sur le mélange œufs-sucre-maïzena. Remettez le tout dans la casserole.',
          duration: 3
        },
        {
          id: '5',
          title: 'Cuisson de la crème',
          description: 'Cuisez à feu moyen en fouettant constamment jusqu\'à épaississement. La crème doit bouillir 2 minutes pour neutraliser l\'amidon.',
          duration: 8,
          temperature: 85
        },
        {
          id: '6',
          title: 'Finalisation',
          description: 'Hors du feu, ajoutez le beurre en morceaux. Fouettez jusqu\'à incorporation complète. Filmez au contact et laissez refroidir.',
          duration: 5
        }
      ]
    }

    setRecipe(mockRecipe)
    setLoading(false)
  }, [params.id])

  // Timer effect
  useEffect(() => {
    let interval: NodeJS.Timeout
    if (isPlaying && timeRemaining > 0) {
      interval = setInterval(() => {
        setTimeRemaining(prev => {
          if (prev <= 1) {
            setIsPlaying(false)
            return 0
          }
          return prev - 1
        })
      }, 1000)
    }
    return () => clearInterval(interval)
  }, [isPlaying, timeRemaining])

  const startTimer = (duration: number) => {
    setTimeRemaining(duration * 60) // Convertir en secondes
    setIsPlaying(true)
  }

  const toggleTimer = () => {
    setIsPlaying(!isPlaying)
  }

  const resetTimer = () => {
    setTimeRemaining(0)
    setIsPlaying(false)
  }

  const nextStep = () => {
    if (currentStep < recipe!.steps.length - 1) {
      setCurrentStep(currentStep + 1)
    }
  }

  const prevStep = () => {
    if (currentStep > 0) {
      setCurrentStep(currentStep - 1)
    }
  }

  const toggleStepComplete = (stepIndex: number) => {
    const newCompleted = new Set(completedSteps)
    if (newCompleted.has(stepIndex)) {
      newCompleted.delete(stepIndex)
    } else {
      newCompleted.add(stepIndex)
    }
    setCompletedSteps(newCompleted)
  }

  const progress = recipe ? ((completedSteps.size / recipe.steps.length) * 100) : 0

  if (loading) {
    return (
      <div className="min-h-screen bg-background">
        <Navbar />
        <div className="container mx-auto p-6">
          <div className="space-y-6">
            <div className="h-8 bg-muted rounded animate-pulse w-1/3" />
            <div className="h-64 bg-muted rounded animate-pulse" />
          </div>
        </div>
      </div>
    )
  }

  if (!recipe) {
    return (
      <div className="min-h-screen bg-background">
        <Navbar />
        <div className="container mx-auto p-6 text-center">
          <h1 className="text-2xl font-bold mb-4">Recette non trouvée</h1>
          <Button asChild>
            <Link href="/recettes">
              <ArrowLeft className="mr-2 h-4 w-4" />
              Retour aux recettes
            </Link>
          </Button>
        </div>
      </div>
    )
  }

  const currentStepData = recipe.steps[currentStep]

  return (
    <div className="min-h-screen bg-background">
      <Navbar />
      
      <div className="container mx-auto p-6">
        {/* Navigation */}
        <div className="mb-6">
          <Button asChild variant="ghost">
            <Link href={`/recettes/${recipe.id}`}>
              <ArrowLeft className="mr-2 h-4 w-4" />
              Retour à la recette
            </Link>
          </Button>
        </div>

        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold mb-2">{recipe.title}</h1>
          <p className="text-muted-foreground mb-6">{recipe.description}</p>
          
          {/* Progress */}
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <span className="text-sm font-medium">Progression</span>
              <span className="text-sm text-muted-foreground">
                {completedSteps.size} / {recipe.steps.length} étapes
              </span>
            </div>
            <Progress value={progress} className="h-2" />
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Étape actuelle */}
          <div className="lg:col-span-2">
            <Card>
              <CardHeader>
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle className="text-2xl">
                      Étape {currentStep + 1} sur {recipe.steps.length}
                    </CardTitle>
                    <CardDescription>{currentStepData.title}</CardDescription>
                  </div>
                  <Badge 
                    variant={completedSteps.has(currentStep) ? 'default' : 'outline'}
                    className="text-lg px-3 py-1"
                  >
                    {completedSteps.has(currentStep) ? 'Terminée' : 'En cours'}
                  </Badge>
                </div>
              </CardHeader>
              <CardContent className="space-y-6">
                {/* Image placeholder */}
                <div className="aspect-video bg-muted rounded-lg flex items-center justify-center">
                  <ChefHat className="h-16 w-16 text-muted-foreground" />
                </div>
                
                {/* Description */}
                <p className="text-lg leading-relaxed">{currentStepData.description}</p>
                
                {/* Infos de l'étape */}
                <div className="flex gap-4">
                  {currentStepData.duration && (
                    <div className="flex items-center space-x-2">
                      <Clock className="h-5 w-5 text-muted-foreground" />
                      <span className="text-sm">{currentStepData.duration} minutes</span>
                    </div>
                  )}
                  {currentStepData.temperature && (
                    <div className="flex items-center space-x-2">
                      <Thermometer className="h-5 w-5 text-muted-foreground" />
                      <span className="text-sm">{currentStepData.temperature}°C</span>
                    </div>
                  )}
                </div>
                
                {/* Timer */}
                {currentStepData.duration && (
                  <Card className="bg-muted/50">
                    <CardContent className="p-4">
                      <div className="flex items-center justify-between">
                        <div>
                          <h4 className="font-semibold">Minuteur</h4>
                          <p className="text-sm text-muted-foreground">
                            {Math.floor(timeRemaining / 60)}:{(timeRemaining % 60).toString().padStart(2, '0')}
                          </p>
                        </div>
                        <div className="flex gap-2">
                          {timeRemaining === 0 ? (
                            <Button onClick={() => startTimer(currentStepData.duration!)}>
                              <Play className="h-4 w-4 mr-2" />
                              Démarrer
                            </Button>
                          ) : (
                            <>
                              <Button onClick={toggleTimer} variant="outline">
                                {isPlaying ? <Pause className="h-4 w-4" /> : <Play className="h-4 w-4" />}
                              </Button>
                              <Button onClick={resetTimer} variant="outline">
                                <RotateCcw className="h-4 w-4" />
                              </Button>
                            </>
                          )}
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                )}
                
                {/* Actions */}
                <div className="flex gap-4">
                  <Button
                    onClick={() => toggleStepComplete(currentStep)}
                    variant={completedSteps.has(currentStep) ? 'outline' : 'default'}
                    className="flex-1"
                  >
                    <Check className="mr-2 h-4 w-4" />
                    {completedSteps.has(currentStep) ? 'Marquer comme non terminée' : 'Marquer comme terminée'}
                  </Button>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Sidebar */}
          <div className="space-y-6">
            {/* Navigation des étapes */}
            <Card>
              <CardHeader>
                <CardTitle>Étapes</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  {recipe.steps.map((step, index) => (
                    <div
                      key={step.id}
                      className={`p-3 rounded-lg border cursor-pointer transition-colors ${
                        index === currentStep
                          ? 'border-primary bg-primary/5'
                          : completedSteps.has(index)
                          ? 'border-green-500 bg-green-50 dark:bg-green-950'
                          : 'border-muted hover:border-muted-foreground/50'
                      }`}
                      onClick={() => setCurrentStep(index)}
                    >
                      <div className="flex items-center space-x-3">
                        <div className={`w-6 h-6 rounded-full flex items-center justify-center text-xs font-semibold ${
                          completedSteps.has(index)
                            ? 'bg-green-500 text-white'
                            : index === currentStep
                            ? 'bg-primary text-primary-foreground'
                            : 'bg-muted text-muted-foreground'
                        }`}>
                          {completedSteps.has(index) ? <Check className="h-3 w-3" /> : index + 1}
                        </div>
                        <div className="flex-1 min-w-0">
                          <p className="text-sm font-medium truncate">{step.title}</p>
                          {step.duration && (
                            <p className="text-xs text-muted-foreground">{step.duration} min</p>
                          )}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>

            {/* Navigation */}
            <Card>
              <CardContent className="p-4">
                <div className="flex gap-2">
                  <Button
                    onClick={prevStep}
                    disabled={currentStep === 0}
                    variant="outline"
                    className="flex-1"
                  >
                    <ArrowLeft className="h-4 w-4 mr-2" />
                    Précédent
                  </Button>
                  <Button
                    onClick={nextStep}
                    disabled={currentStep === recipe.steps.length - 1}
                    className="flex-1"
                  >
                    Suivant
                    <ArrowRight className="h-4 w-4 ml-2" />
                  </Button>
                </div>
              </CardContent>
            </Card>

            {/* Ingrédients */}
            <Card>
              <CardHeader>
                <CardTitle>Ingrédients</CardTitle>
                <CardDescription>Pour {recipe.servings} portions</CardDescription>
              </CardHeader>
              <CardContent>
                <ul className="space-y-2">
                  {recipe.ingredients.map((ingredient, index) => (
                    <li key={index} className="flex items-start space-x-2">
                      <div className="w-2 h-2 bg-primary rounded-full mt-2 flex-shrink-0" />
                      <span className="text-sm">{ingredient}</span>
                    </li>
                  ))}
                </ul>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  )
}
